body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#footer {
    width: 100%;
    height: auto;
    word-wrap: break-word;
    color: var(--secondary-color);
    text-align: center;
    font-size: 1.5rem;
    bottom: 0;
    background-color: var(--primary-color);
    padding: 10px;
  }
  
  .footerDetails {
    font-size: 12px;
    text-align: end;
  }
  
  .footerSayKaren {
    height: 3.5rem;
  }
  
  .footer_icon {
    margin-right: 10px;
    float: right;
  }
  
  .music {
    margin-top: 20px;
    opacity: 0.65;
  }
  
:root {
  --primary-color: #282c34;
  --secondary-color: #61dafb;
  --light-color: white;
  --dark-color: black;
}

a {
  color: white;
  color: var(--light-color);
}

.App {
  text-align: center;
  min-height: 90vh;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  background-color: #61dafb;
}

html {
  background-color: #61dafb;
  height: 100vh;
}

.imageGroup {
  max-width: 80vw;
  box-shadow: 1px 1px 1px white;
  box-shadow: 1px 1px 1px var(--light-color);
  border-radius: 10px;
}

#inputSearchTerm {
  margin: 5px;
  border-radius: 4px;
  padding: 2px;
}

code {
  color: #61dafb;
  color: var(--secondary-color);
  text-shadow: rgba(255, 255, 255, 0.3) 1px 1px;
  font-family: Arial, Helvetica, sans-serif;
}

.imageSmallGroup {
  max-width: 10vw;
  box-shadow: 1px 1px 1px white;
  box-shadow: 1px 1px 1px var(--light-color);
  border-radius: 10px;
}

.headers {
  padding-top: 20px;
}

.tableColumn {
  width: 30%;
  margin: 15px;
  padding: 5px;
 border: 5px solid #282c34;
 border: 5px solid var(--primary-color);
 box-shadow: inset 1px 1px white;
 box-shadow: inset 1px 1px var(--light-color);
 border-radius: 5px;
}

.mainSection {
  padding: 10px;
}

.title {
  text-align: center;
}

